/* Scroll to top style */
.scrollToTop {
  @apply bg-indigo-600;
  @apply text-white;
  position: fixed;
  width: 100%;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 999;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.9;
}

.scrollToTop:hover {
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
.social-link {
  display: inline-block;
  margin-right: 0.5rem;
}

.social-icon {
  font-size: 3rem;
  color: #007bff;
  transition: color 0.3s;
}

.social-link:hover .social-icon {
  color: #0056b3;
}
.icon-about {
  color: #5ca9fb;
  font-size: 20px;
  margin-bottom: 1px;
  margin: 1px;
}
.heart {
  color: rgb(172, 11, 11);
  animation: scale-infinite 2s infinite;
}
@keyframes scale-infinite {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}

.plugin-contact {
  background-color: #4c90d934;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(97, 95, 95, 0.822);
  border: 0.2px solid rgb(100, 100, 100);
}

/*************** Mine*/
.section-title {
  position: relative;
  text-align: center;
  padding-bottom: 20px;
}

.section-title::after {
  content: "";
  background: linear-gradient(to right, #5ca9fb 0%, #6372ff 100%);
  height: 4px;
  width: 60px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.3s ease;
}

.section-title:hover::after {
  width: 0px;
}

.nav-item {
  position: relative;
}

.nav-item::after {
  content: "";
  background: linear-gradient(to right, #5ca9fb 0%, #6372ff 100%);
  height: 4px;
  width: 0;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.3s ease;
}

.nav-item:hover::after {
  width: 60px;
}

/* You can adjust the colors to match your desired gradient effect */
.bg-gradient-to-b {
