/* AboutMeBio.css */

/* Styles for the circular image container */
.rounded-image-container {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  animation: border-pulse 2s linear infinite;
  box-shadow: 0 0 10px rgba(48, 131, 214, 0.829);
}

@media screen and (max-width: 600px) {
  .rounded-image-container {
    width: 170px !important;
    height: 170px !important;
  }
}

/* Style for the circular image */
.circular-image img {
  padding: 5px;

  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Animation keyframes for the border pulse */
@keyframes border-pulse {
  0% {
    border: 2px solid transparent; /* Start with a transparent border */
  }
  50% {
    border: 2px solid #3084d6; /* Pulse to a color (e.g., #00bcd4) */
  }
  100% {
    border: 2px solid transparent; /* Return to a transparent border */
  }
}
